"use client"
import { useCategory } from "@/contexts/Category"
import { useState } from "react"
import { ProductCreate } from "@/interfaces/Product"

interface Props {
    product: {
        id: number
        name: string
        category: string
        price: number
        cost: number
        stock: number
    }
}
const defaultInputStyle = "p-2 border rounded"

function ProductForm(props: Props) {
    const { product } = props
    const { categoryList } = useCategory()
    const [thisProduct, setThisProduct] = useState<ProductCreate>({
        name: "",
        category: 0,
        price: 0,
        cost: 0,
        stock: 0,
    })

    const setProduct = () => {
        if (!props) {
            setThisProduct({
                name: "",
                category: 0,
                price: 0,
                cost: 0,
                stock: 0,
            })
        } else {
            setThisProduct(product)
        }
    }

    const handleInputChange = (
        e: React.ChangeEvent<HTMLInputElement | HTMLSelectElement>,
    ) => {
        const { id, value } = e.target

        setThisProduct((prev) => ({
            ...prev,
            [id]:
                id === "category" ||
                id === "price" ||
                id === "cost" ||
                id === "stock"
                    ? Number(value)
                    : value,
        }))
    }

    const handleSubmit = (e: React.FormEvent) => {
        e.preventDefault()
    }
    return (
        <form onSubmit={handleSubmit} className="flex flex-col">
            <div className="flex flex-col p-2">
                <label htmlFor="name">Product Name</label>
                <input
                    id="name"
                    type="text"
                    value={thisProduct.name}
                    onChange={handleInputChange}
                    className={defaultInputStyle}
                />
            </div>
            <div className="flex flex-col p-2">
                <label htmlFor="category">Category</label>
                <select
                    id="category"
                    value={thisProduct.category}
                    className={defaultInputStyle}
                    onChange={handleInputChange}
                >
                    {categoryList.map((cat) => {
                        return (
                            <option key={cat.id} value={cat.id}>
                                {cat.name}
                            </option>
                        )
                    })}
                </select>
            </div>
            <div className="flex flex-col p-2">
                <label htmlFor="price">Price</label>
                <input
                    id="price"
                    type="number"
                    value={thisProduct.price}
                    onChange={handleInputChange}
                    className={defaultInputStyle}
                />
            </div>
            <div className="flex flex-col p-2">
                <label htmlFor="cost">Cost</label>
                <input
                    id="cost"
                    type="number"
                    value={thisProduct.cost}
                    onChange={handleInputChange}
                    className={defaultInputStyle}
                />
            </div>
            <div className="flex flex-col p-2">
                <label htmlFor="stock">Stock</label>
                <input
                    id="stock"
                    type="number"
                    value={thisProduct.stock}
                    onChange={handleInputChange}
                    className={defaultInputStyle}
                />
            </div>
        </form>
    )
}
export default ProductForm
